.discuss-look {
  background: #2a273b;
  width: 100%;
  height: calc(100% - 100px);
  padding: 19px;
  display: flex;
}
.discuss-look .team-score-left {
  position: relative;
  height: calc(100vh - 140px);
  padding-left: 51px;
  flex: 1;
}
.discuss-look .team-score-left > p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  padding-left: 8px;
}
.discuss-look .team-score-left > div {
  min-width: 771px;
}
.discuss-look .team-score-left .leftList {
  width: 330px;
  height: 90px;
  background: #302e45;
  box-shadow: 0px 2px 10px 0px #232031;
  margin: 20px 27px 0px 28px;
}
.discuss-look .team-score-left .leftList .ant-col {
  text-align: center;
  padding: 15px 0;
  font-size: 12px;
}
.discuss-look .team-score-left .leftList .ant-col img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.discuss-look .team-score-left .leftList .ant-col h4 {
  color: rgba(255, 255, 255, 0.8);
  text-align: left;
  margin: 7px 0;
}
.discuss-look .team-score-left .leftList .ant-col p {
  color: #fff;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.discuss-look .team-score-left .leftList .ant-col:nth-of-type(3) {
  color: rgba(255, 255, 255, 0.8);
  border-left: 1px solid rgba(0, 0, 0, 0.2);
  height: 60px;
  margin-top: 20px;
}
.discuss-look .team-score-right {
  color: rgba(255, 255, 255, 0.8);
  width: 280px;
}
.discuss-look .team-score-right > div {
  background: #302e45;
  padding: 0 20px;
}
.discuss-look .team-score-right > div h3 {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.discuss-look .team-score-right > div h3 span {
  margin: 0 5px;
}
.discuss-look .team-score-right > div:nth-of-type(1) {
  min-height: 36%;
  height: 36%;
  margin-bottom: 20px;
}
.discuss-look .team-score-right > div:nth-of-type(1) p {
  text-align: center;
}
.discuss-look .team-score-right > div:nth-of-type(1) .ant-progress-inner {
  width: 150px !important;
  height: 150px !important;
}
.discuss-look .team-score-right > div:nth-of-type(1) .ant-progress-text {
  color: #fff;
}
.discuss-look .team-score-right > div:nth-of-type(2) {
  height: calc(64% - 20px);
}
.discuss-look .team-score-right > div:nth-of-type(2) .scrollBar {
  height: calc(100% - 53px);
}
.discuss-look .team-score-right > div:nth-of-type(2) .ant-col {
  margin: 7px 0;
  line-height: 30px;
}
.discuss-look .team-score-right > div:nth-of-type(2) .ant-col:nth-of-type(2) img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.discuss-look .team-score-left .leftList {
  float: left;
  height: 100px;
}
.discuss-look-list {
  height: calc(100% - 109px);
  width: 100%;
  overflow: auto;
}
.discuss-look-list::-webkit-scrollbar {
  display: none;
}
.vote_process_box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: calc(100% - 53px);
}
.discuss-look-detail .ant-modal-close-x {
  display: none;
}
.discuss-look-detail .ant-modal-footer > div {
  width: 100%;
}
.discuss-look-detail .ant-modal-footer > div .ant-btn:nth-child(1) {
  display: none;
}
.discuss-look-detail .ant-modal-footer .ant-btn-primary {
  border-color: white;
}
